import i18next from "i18next"
import React from "react"
import styled from "styled-components"
const WrapUI = styled.div`
  background: white;
  padding: 1rem;
  text-align: center;
  & > .header {
    font-weight: bold;
    color: #dc3545;
    font-size: 1.5rem;
  }
  & > .body {
    padding: 5px 0px;
  }
`
export default function MUIMsgBox(props) {
  return (
    <WrapUI className="mui-msgbox_error">
      <div className="header">{i18next.t("Error!")}</div>
      <div className="body">{i18next.t("Something went wrong")}</div>
    </WrapUI>
  )
}
